import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateModal, updateProject } from "../projectSlice";
import {
    Navbar,
    NavbarBrand,
    Nav,
    NavItem,
    Dropdown,
    UncontrolledDropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";

export default function NavBar() {
    const projects = useSelector(state => state.project.projects);
    const currentProjectIndex = useSelector(state => state.project.currentProjectIndex);
    const currentProject = projects[currentProjectIndex];
    const dispatch = useDispatch();
    const [menuOpen, setMenuOpen] = useState([false, false, false, false, false, false]);
    const os = navigator?.userAgent?.platform || navigator?.platform;
    const isMac = /mac|iphone|ipad|ipod/.test(os);

    const isOptionDisabled = () => projects.length === 0;
    const setModal = name => () => dispatch(updateModal(name));

    const toggleMenu = index => {
        const nextMenu = menuOpen.map((e, i) => {
            if (index < 0) {
                if (menuOpen[i] === true) {
                    return true;
                }
                if (menuOpen[i-1] === true) {
                    return true;
                }
                return false;
            }
            else if (i === index) {
                return !menuOpen[index];
            }
            return false;
        });
        setMenuOpen(nextMenu);
    }
    const hoverMenu = index => () => {
        if (menuOpen.includes(true)) {
            toggleMenu(index);
        }
    }

    return (
        <Navbar className="navbar-fixed-top" expand="xl">
            <Nav navbar style={{minWidth: "100% !important"}}>
                <Dropdown nav isOpen={menuOpen[0]} toggle={() => toggleMenu(0)}>
                    <DropdownToggle nav onMouseOver={hoverMenu(0)}>File</DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem onClick={setModal("project")}>New</DropdownItem>
                            <DropdownItem divider />
                            <DropdownItem>Open...</DropdownItem>
                            <DropdownItem divider />
                            <DropdownItem disabled={isOptionDisabled()}>Save</DropdownItem>
                            <DropdownItem onClick={setModal("export")} disabled={isOptionDisabled()}>Export...</DropdownItem>
                            <DropdownItem divider />
                            <DropdownItem disabled={isOptionDisabled()}>Print...</DropdownItem>
                        </DropdownMenu>
                </Dropdown>
                <Dropdown nav isOpen={menuOpen[1]} toggle={() => toggleMenu(1)}>
                    <DropdownToggle nav onMouseOver={hoverMenu(1)}>Edit</DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem disabled={isOptionDisabled()}>Undo</DropdownItem>
                            <DropdownItem disabled={isOptionDisabled()}>Redo</DropdownItem>
                            <DropdownItem divider />
                            <DropdownItem disabled={isOptionDisabled()}>Cut</DropdownItem>
                            <DropdownItem disabled={isOptionDisabled()}>Copy</DropdownItem>
                            <DropdownItem disabled={isOptionDisabled()}>Paste</DropdownItem>
                            <DropdownItem disabled={isOptionDisabled()}>Delete</DropdownItem>
                        </DropdownMenu>
                </Dropdown>
                <Dropdown nav isOpen={menuOpen[2]} toggle={() => toggleMenu(2)}>
                    <DropdownToggle nav onMouseOver={hoverMenu(2)}>Tools</DropdownToggle>
                    <DropdownMenu>
                        <DropdownItem disabled={isOptionDisabled()}>Select</DropdownItem>
                        <DropdownItem disabled={isOptionDisabled()}>Move</DropdownItem>
                        <DropdownItem divider />
                        <DropdownItem disabled={isOptionDisabled()}>Circle</DropdownItem>
                        <DropdownItem disabled={isOptionDisabled()}>Rectangle</DropdownItem>
                        <DropdownItem disabled={isOptionDisabled()}>Hexagon</DropdownItem>
                        <DropdownItem disabled={isOptionDisabled()}>Trapeze</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
                <Dropdown nav isOpen={menuOpen[3]} toggle={() => toggleMenu(3)}>
                    <DropdownToggle nav onMouseOver={hoverMenu(3)}>View</DropdownToggle>
                    <DropdownMenu>
                        <DropdownItem disabled={isOptionDisabled()}>Zoom in</DropdownItem>
                        <DropdownItem disabled={isOptionDisabled()}>Zoom out</DropdownItem>
                        <DropdownItem
                            onClick={() => dispatch(updateProject({
                                index: currentProjectIndex,
                                project: {
                                    ...currentProject,
                                    position: {
                                        top: "50%",
                                        left: "50%",
                                    }
                                }
                            }))}
                            disabled={isOptionDisabled()}
                        >
                            Fit The Area
                        </DropdownItem>
                    </DropdownMenu>
                </Dropdown>
                <Dropdown nav isOpen={menuOpen[4]} toggle={() => toggleMenu(4)}>
                    <DropdownToggle nav onMouseOver={hoverMenu(4)}>More</DropdownToggle>
                    <DropdownMenu>
                        <Dropdown nav isOpen={menuOpen[5]} toggle={() => toggleMenu(-1)}>
                            <DropdownToggle nav onMouseOver={hoverMenu(-1)} className="d-flex justify-content-between">
                                Theme 
                                <p>&rsaquo;</p>
                            </DropdownToggle>
                            <DropdownMenu className="menu-right">
                                <DropdownItem onClick={() => setTheme("light")}>Light</DropdownItem>
                                <DropdownItem onClick={() => setTheme("dark")}>Dark</DropdownItem>
                                </DropdownMenu>
                        </Dropdown>
                        <DropdownItem onClick={setModal("shortcuts")}>Keyboard shortcuts</DropdownItem>
                        <DropdownItem tag="a" href="https://github.com/velickovicdj/dialgo" target="_blank">Report a Bug</DropdownItem>
                        <DropdownItem divider />
                        <DropdownItem onClick={setModal("about")}>About Dialgo</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            </Nav>
            {/* <NavbarBrand href="/">dialgo</NavbarBrand> */}
        </Navbar>
    );
}