import React, { lazy, Suspense } from "react";
import ReactDOM from "react-dom/client";
import { Provider, useDispatch, useSelector } from "react-redux";
import { configureStore } from "@reduxjs/toolkit";
import projectReducer from "./projectSlice.js";
import { updateCursor } from "./projectSlice.js";

// Development
import { UncontrolledAlert } from "reactstrap";

import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import "bootstrap-icons/font/bootstrap-icons.css"
import "./assets/scss/dialgo.scss";

import NavBar from "./components/NavBar.js";
import Landing from "./views/Landing.js";

const Toolbar = lazy(() => import("./components/Toolbar.js"));
const Tabs = lazy(() => import("./components/Tabs.js"));
const Canvas = lazy(() => import("./views/Canvas.js"));
const DModal = lazy(() => import("./components/modal/index.js"));

const store = configureStore({
    reducer: {
        project: projectReducer
    }
});

const App = () => {
    const projects = useSelector(state => state.project.projects);
    const cursor = useSelector(state => state.project.cursor);
    const dispatch = useDispatch();
    
    const setCursor = type => dispatch(updateCursor(type));
    
    document.onkeydown = e => {
        if (cursor === "zoom-in" && e.key === "Alt") {
            return setCursor("zoom-out");
        }

        switch (e.key?.toLocaleLowerCase()) {
            case 'v': 
                return setCursor("default");
            case ' ': 
                return setCursor("grab");
            case 'z': 
                return setCursor("zoom-in");
            default: 
                break;
        }
    }
    document.onkeyup = e => (cursor === "zoom-out" && e.key === "Alt") ? setCursor("zoom-in") : null;

    return(
        <>
            {/* Development */}
            <UncontrolledAlert color="danger">
                <span className="fw-bold">WARNING:</span> Dialgo is currently under development and is not yet suitable for regular use. 
                It may contain bugs, errors, and incomplete features that could impact your experience. Please use at your own risk.
            </UncontrolledAlert>
            <NavBar />
            {projects.length > 0 ? (
                <>
                    <Suspense fallback={null}>
                        <Toolbar />
                        <Tabs />
                        <Canvas />
                    </Suspense>
                </> 
            ) : <Landing />
            }
            <Suspense fallback={null}>
                <DModal />
            </Suspense>
        </>
    );
}

const root = ReactDOM.createRoot(document.getElementById("root"));

root.render(
    <Provider store={store}>
        <App />
    </Provider>,
);