  .outline{\n                cursor: pointer;\n                margin: auto;\n                border: 1px solid #000;\n            }\n        }\n    }\n    object{\n        width: 100%;\n        height: 100%;\n    }\n    .modal-header{\n        cursor: default;\n        height: 40px;\n    }\n    .modal-body.preview{\n        padding-top: 0;\n        padding-bottom: 0;\n        padding-left: 0;\n\n        .row:not(.row:last-child),\n        .row .row:last-child{\n            margin-bottom: 25px;\n        }\n    }\n    label{\n        margin-bottom: 0;\n    }\n    input{\n        cursor: text;\n    }\n}",".navbar-fixed-top {\n    flex-wrap: nowrap;\n    width: 100%;\n    height: $navbar-height;\n    background-color: #ffffff;\n\n    .navbar-nav {\n        display: flex;\n        flex-direction: row;\n        align-items: center;\n        position: absolute;\n        top: 0;\n        left: 5px;\n        height: 100%;\n\n        &::-webkit-scrollbar {\n            display: none;\n        }\n\n        & > * {\n            cursor: default !important;\n        }\n    \n        .dropdown-toggle::after {\n            display: none;\n        }\n    \n        .dropdown-menu {\n            margin-top: -5px;\n            border: none;\n            border-top-left-radius: 0;\n            border-top-right-radius: 0;\n            box-shadow: $shadow-light;\n        }\n\n        .menu-right {\n            margin-top: -45px;\n            margin-left: 165px;\n        }\n    \n        .nav-item, .nav-link,\n        .dropdown-item {\n            font-size: $navbar-font-size;\n        }\n    \n        .nav-link {\n            padding-right: 0.5rem !important;\n            padding-left: 0.5rem !important;\n        }\n    }\n}","$navbar-height: 30px;\n$navbar-font-size: 14px;\n\n$shadow-light: 0 20px 50px rgba(0, 0, 0, 0.15);\n\n",".toolbar{\n    position: fixed;\n    z-index: 8;\n    top: 50%;\n    transform: translateY(-50%);\n    left: 10px;\n    width: 50px;\n    border: 1px solid black;\n    border-radius: 10px;\n    background-color: white;\n    // color: #fff;\n\n    .navbar-nav {\n        padding: 5px;\n       \n        .dropdown-menu{\n            margin-left: 25px;\n            min-width: 25px;\n            border-radius: 10px;\n\n            button:not(button:first-child){\n                margin-top: 12.5px;\n            }\n        }\n        .nav-item:not(.nav-item:first-child){\n            margin-top: 25px;\n        }\n    }\n}",".tabs::-webkit-scrollbar {\n    width: 5px;\n    height: 5px;\n}\n\n.tabs::-webkit-scrollbar-thumb {\n    background-color: white;\n}\n\n.tabs::-webkit-scrollbar-track {\n    background-color: transparent;\n}\n.tabs{\n    z-index: 999;\n    display: flex;\n    flex-wrap: nowrap;\n    overflow-x: auto;\n    overflow-y: hidden;\n    -webkit-overflow-scrolling: touch; /* for smooth scrolling on iOS */\n    white-space: nowrap;\n    position: relative;\n    padding: 2.5px 10px;\n    width: 100%;\n    height: $navbar-height;\n    border: none;\n    background-color: #eeeeee;\n\n    .nav-link{\n        cursor: default;\n        overflow: hidden;\n        display: flex;\n        flex-shrink: 0;\n        flex-basis: 0;\n        flex-grow: 1;\n        justify-content: center;\n        align-items: center;\n        margin-right: 5px;\n        height: $navbar-height - 5.5px;\n        max-width: 100%;\n        font-size: 14px;\n        border-radius: 5px;\n        border: none;\n        color: gray;\n\n\n        &:hover:not(.active){\n            background-color: #00000010;\n        }\n\n        .btn-close{\n            margin-left: .5em;\n            width: .5em;\n            height: .5em;\n        }\n\n        flex: 1;\n    }\n}",".canvas-wrapper{\n    overflow: hidden;\n    position: relative;\n    width: 100%;\n    height: calc(100vh - calc($navbar-height * 2));\n    \n    .fabric-wrapper{\n        cursor: inherit;\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        transform: translate(-50%, -50%);\n        background-color: #ffffff;\n        box-shadow: $shadow-light;\n    }\n}\n.slider{\n    position: fixed;\n    bottom: calc($navbar-height / 2);\n    right: calc($navbar-height);\n    z-index: 999;\n}\n","\n    .drop-area{\n        margin-top: 25px;\n    }\n    .drop-area{\n        width: 75%;\n        height: 200px;\n        border: 1px solid lightGray;\n        border-radius: 10px;\n        border-style: dashed;\n    }","@import \"variables.scss\";\n\n// Components\n\n@import \"modals.scss\";\n@import \"navbar.scss\";\n@import \"toolbar.scss\";\n@import \"tabs.scss\";\n@import \"canvas.scss\";\n@import \"card.scss\";\n\nbody {\n    overflow-x: hidden;\n    background-color: #eeeeee;\n}"],"sourceRoot":""}]);
